<template>
  <transition name="top">
  <div class="toplist-page">
      <prevheader :title='title'></prevheader>
      <div class="load" v-show="load"><loading></loading></div>
      <scroll :data='toplist' :pullup='pullup' @scrollToEnd='loadmore()' v-show="!load">
        <ul class="list-content">
          <li v-for="(list, index) in toplist" class="list" @click="fileLink(list, 1, toplist)">
            <div class="rank">
              <span v-if="index <= 2" :class='{icon: true, icon1: index===0, icon2: index===1, icon3: index===2}'></span>
              <span v-else class="text">{{index + 1}}</span>
            </div>
            <div class="content">
              <h2 class="name">{{list.title}}</h2> 
              <p class="desc">{{list.artist_name}}-{{list.country}}</p>
            </div>
          </li>
          
          <li v-if="!noneData"><loading></loading></li>
          <li v-else style="text-align:center;margin-top:20px;font-size:13px;">没有更多数据</li>
        </ul>
      </scroll>
  </div>
  </transition>
</template>

<script>
  import loading from '../components/loading/loading'
  import scroll from '../components/scroll/scroll'
  import serve from '../serve'
  import utils from '../common/js/utils'
  import prevheader from '@/components/prevheader/prevheader'
  export default {
    data () {
      return {
        title: '排行榜',
        toplist: [],
        load: true,
        pullup: true,
        noneData: false
      }
    },
    created () {
      switch (parseInt(this.$route.params.type)) {
        case 1:
        this.title = '新歌榜'
        break
        case 2:
        this.title = '热歌榜'
        break
        case 11:
        this.title = '摇滚榜'
        break
        case 12:
        this.title = '爵士'
        break
        case 16:
        this.title = '流行'
        break
        case 21:
        this.title = '欧美金曲榜'
        break
        case 22:
        this.title = '经典老歌榜'
        break
        case 23:
        this.title = '情歌对唱榜'
        break
        case 24:
        this.title = '影视金曲榜'
        break
        case 25:
        this.title = '网络歌曲榜'
        break
        default:
        utils.prev()
      }
      setTimeout(() => {
        this.getData(50)
        .then(res => {
          this.load = false
          this.toplist = res.body.song_list
        })
      }, 300)
    },
    methods: {
      getData(num = 20, offset = this.toplist.length) {
        let option = {
            method: 'baidu.ting.billboard.billList',
            type: this.$route.params.type,
            size: num,
            offset: offset
        }
        return serve.get(option)
      },
      fileLink(id, type, data) {
          utils.getSong(id, type, data)
      },
      loadmore() {
        if (this.pullup) {
          this.pullup = false
          this.getData()
          .then((res) => {
            if (!res.body.song_list || !res.body.song_list.length) {
              this.noneData = true
              return
            }
            this.toplist = this.toplist.concat(res.body.song_list)
            this.pullup = true
          })
        }
      }
    },
    components: {
      prevheader,
      scroll,
      loading
    }
  }
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style lang="scss" scoped>
@import '../common/scss/var';
.toplist-page{
  position: absolute;
  top: 0;
  bottom: 0;
  left: 0;
  right: 0;
  background: $dayBg;
  z-index: 10;
  padding: 0 15px;
}
.list-content{
  padding: 55px 0;
  .list{
    display: flex;
    align-items: center;
    box-sizing: border-box;
    height: 64px;
    font-size: 14px;
  }
  .rank{
    flex: 0 0 25px;
    width: 25px;
    margin-right: 30px;
    text-align: center;
    color: $dayTheme;
    font-size: 18px;
    .icon{
      display: block;
      height: 24px;
      background-size: 25px 24px;
    }
    .icon1{
      background-image: url(data:image/png;base64,)
    }
    .icon2{
      background-image: url(data:image/png;base64,)
    }
    .icon3{
      background-image: url(data:image/png;base64,)
    }
  }
  .content{
    flex: 1;
    line-height: 20px;
    overflow: hidden;
  }
  .name{
    color: $day-color;
    font-size: 14px;
    font-weight: normal;
    text-overflow: ellipsis;
    overflow: hidden;
    white-space: nowrap;
  }
  .desc{
    font-size: 13px;
    margin-top: 3px;
    color: #999;
    text-overflow: ellipsis;
    overflow: hidden;
    white-space: nowrap;
  }
}
.load{
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
}
.top-enter-active, .top-leave-active {
  transition: all .3s
}
.top-enter, .top-leave-active {
  transform: translate3d(100%,0,0);
}
</style>



